<template>
  <!-- 高级参照详情和新增 -->
  <mrc_ess_skLoaders :skLoading='skLoading'>
    <div data-app='true'
         class="advance">
      <mrc_ess_iconGroup :is_disabled="disabled"
                         @handleReturn='handleReturn'
                         @handleEdit='handleEdit'
                         @handleDel='handleDel'></mrc_ess_iconGroup>
      <v-form ref="advanceReferTo"
              v-model="advanceReferTo">
        <v-card style='padding: 10px 40px'>
          <v-row v-if="$store.state.uuid">
            <v-col cols="12"
                   sm="12">
              <div style='float: right'>
                <template v-for="item in reqParams.client_type_uuid">
                  <mrc_ess_svgIcon :key='item'
                                   style="font-size: 24px;margin-right: 5px"
                                   :icon-class='item'></mrc_ess_svgIcon>
                </template>
              </div>
            </v-col>
          </v-row>
          <v-row>
            <!-- <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <v-text-field :label="$t('label_With_reference_to_the_name') + '*'"
                            required
                            v-model="reqParams.prompt_name"
                            :readonly="disabled"
                            :rules="$text_rules($t('label_With_reference_to_the_name'))"></v-text-field>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <v-text-field :label="$t('label_type') + '*'"
                            required
                            :readonly="disabled"
                            v-model="reqParams.prompt_type"
                            :rules="$text_rules($t('label_type'))"></v-text-field>
            </v-col> -->
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <mrc_ess_multiText :label="$t('label_With_reference_to_the_name') + '*'"
                                 required
                                 style="width:100%"
                                 v-model="reqParams.prompt_name"
                                 :isNational="judegNational('prompt_name')"
                                 :readonly="disabled"
                                 :rules="$text_rules($t('label_With_reference_to_the_name'))"></mrc_ess_multiText>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <mrc_ess_multiText :label="$t('label_type') + '*'"
                                 required
                                 style="width:100%"
                                 :readonly="disabled"
                                 v-model="reqParams.prompt_type"
                                 :isNational="judegNational('prompt_type')"
                                 :rules="$text_rules($t('label_type'))"></mrc_ess_multiText>
            </v-col>
          </v-row>
          <v-row>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <v-textarea v-model="reqParams.prompt_desc"
                          :auto-grow="true"
                          :readonly='disabled'
                          :clearable="false"
                          :label="$t('label_describe')"
                          row-height="24"
                          rows="1"></v-textarea>
            </v-col>
          </v-row>
          <v-row>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <mrc_ess_dictName v-model="reqParams.client_type_uuid"
                                :disabled='disabled'
                                :label="$t('label_Client_type') + '*'"
                                :rules="false"
                                multiple
                                :errMsg="$errors('reqParams','client_type_uuid',requiredRule)"
                                dict_name='client_type'
                                chips></mrc_ess_dictName>
              <!-- <v-select v-model="reqParams.client_type_uuid"
                        :items="client_type"
                        :readonly="disabled"
                        item-value="dict_value"
                        item-text="dict_label"
                        :label="$t('label.client_type') + '*'"
                        required
                        multiple
                        chips
                        v-if="!$store.state.prompt_name"
                        :rules="[v => v.length > 0 || $t('label.client_type')]"></v-select> -->
            </v-col>
          </v-row>
          <v-row>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <!-- <v-select
              v-model="reqParams.source_table"
              :readonly="disabled"
              :items="source_table_name"
              item-value='value_field'
              item-text='view_fields'
              :label="$t('label.source_table') + '*'"
              required
              @change='source_tableChange'
              chips
              :rules="[v => !!v || $t('label.source_table')]"
            ></v-select> -->
              <mrc_ess_prompt :rules="$t('label_The_source_table_name')"
                              :label="$t('label_The_source_table_name') + '*'"
                              :disabled='disabled'
                              :obj='promptData'
                              v-model="reqParams.source_table"
                              @getData='source_tableChange'>
              </mrc_ess_prompt>
            </v-col>
          </v-row>
          <v-row>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <!-- <v-select
              v-model="reqParams.value_field"
              :items="valArr"
              :readonly="disabled"
              item-value='value_field'
              item-text='view_fields'
              chips
              :no-data-text="$t('error.source_table')"
              :rules="[v => !!v || $t('label.line_value')]"
              :label="$t('label.line_value') + '*'"
            ></v-select> -->
              <mrc_ess_prompt :select_arr='valArr'
                              :rules="$t('label_Take_the_column_value')"
                              :disabled='disabled'
                              :label="$t('label_Take_the_column_value') + '*'"
                              :no-data-text="$t('error.source_table')"
                              v-model="reqParams.value_field">
              </mrc_ess_prompt>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <!-- <v-select
              v-model="reqParams.view_fields"
              :items="valArr"
              item-value='value_field'
              item-text='view_fields'
              :label="$t('label.show_line') + '*'"
              :no-data-text="$t('error.source_table')"
              :readonly="disabled"
              chips
              required
              :rules="[v => !!v || $t('label.show_line')]"
            ></v-select> -->
              <mrc_ess_prompt :select_arr='valArr'
                              :rules="$t('label_Show_columns')"
                              :disabled='disabled'
                              :label="$t('label_Show_columns') + '*'"
                              :no-data-text="$t('error.source_table')"
                              v-model="reqParams.view_fields">
              </mrc_ess_prompt>
            </v-col>
          </v-row>
          <v-row dense>
            <!-- <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <v-text-field :label="$t('label_Associated_with_the_unit')"
                            :readonly="disabled"
                            v-model="reqParams.org_filter_field"></v-text-field>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <v-text-field :label="$t('label_Associated_personnel')"
                            :readonly="disabled"
                            v-model="reqParams.user_filter_field"></v-text-field>
            </v-col> -->
            <v-col cols="12"
                   sm="6">
              <mrc_ess_multiText :label="$t('label_Associated_with_the_unit')"
                                 :readonly="disabled"
                                 :isNational="judegNational('org_filter_field')"
                                 v-model="reqParams.org_filter_field"></mrc_ess_multiText>
            </v-col>
            <v-col cols="12"
                   sm="6">
              <mrc_ess_multiText :label="$t('label_Associated_personnel')"
                                 :readonly="disabled"
                                 :isNational="judegNational('user_filter_field')"
                                 v-model="reqParams.user_filter_field"></mrc_ess_multiText>
            </v-col>
          </v-row>
          <v-row>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <!-- multiple -->
              <!-- <v-select
              v-model="reqParams.prompt_field"
              :items="valArr"
              :readonly="disabled"
              item-value='value_field'
              item-text='view_fields'
              :label="$t('label.reference_content')+'*'"
              required
              :rules="[v=>v.length > 0 || $t('error.reference_content')]"
              :no-data-text="$t('error.source_table')"
              multiple
              chips
            ></v-select> -->
              <mrc_ess_prompt :select_arr='valArr'
                              :rules="$t('label_With_reference_to_the_content')"
                              :disabled='disabled'
                              :label="$t('label_With_reference_to_the_content')+'*'"
                              :no-data-text="$t('error.source_table')"
                              v-model="reqParams.prompt_field"
                              multiple>
              </mrc_ess_prompt>
            </v-col>
          </v-row>
          <v-row>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <v-text-field :label="$t('label_conditions')"
                            :readonly="disabled"
                            v-model="reqParams.filter"
                            :rules="[
                  v => String(v).length <= 20000 || '超过输入个数'
              ]"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <!-- <v-select
              v-model="reqParams.orderby"
              :items="valArr"
              item-value='value_field'
              item-text='view_fields'
              :readonly="disabled"
              :no-data-text="$t('error.source_table')"
              :label="$t('label.sort')+'*'"
              chips
              :rules="[v => !!v || $t('label.sort')]"
            ></v-select> -->
              <mrc_ess_prompt :select_arr='valArr'
                              :rules="$t('label_The_sorting')"
                              :disabled='disabled'
                              :label="$t('label_The_sorting')+'*'"
                              :no-data-text="$t('error.source_table')"
                              v-model="reqParams.orderby">
              </mrc_ess_prompt>
            </v-col>
          </v-row>
        </v-card>
        <mrc_ess_submitBtn v-show="!disabled"
                           @cancel='cancel'
                           :loading='loading'
                           @success='success'></mrc_ess_submitBtn>
      </v-form>
      <!-- 删除确认框 -->
      <confirm :isOpen='isOpen'
               @onCancel='onCancel'
               :showText='showText'
               @onConfirm='onConfirm'
               :isLoading='isLoading' />
      <v-snackbar v-model="snackbar"
                  :timeout="timeout"
                  :right='true'
                  :top='true'>
        {{ newsText }}
      </v-snackbar>
    </div>
  </mrc_ess_skLoaders>

</template>

<script>
import { getBusinessList, getDetail, getChange, getAdd, getDetele } from '@/service/api/prompt'
import { getDataByName } from '@/service/api/data_dictionary'
import confirm from '@/components/confirm'
// import myBtns from '@/components/c_s_btns'
// import iconGroup from '@/components/iconGroup'
import { required } from 'vuelidate/lib/validators'

export default {
  components: { confirm },
  props: {},
  data () {
    return {
      loading: false,
      skLoading: true,
      showText: this.$t('info.is_del'),
      is_return: false,
      snackbar: false,
      timeout: 2000,
      newsText: '',
      isLoading: false,
      isOpen: false,
      params: {
        '~table~': 'mrbase_sys_prompt'
      },
      reqParams: { // 这是会被修改的 修改失败就把之前的还原  temp->reqParams
        '~table~': 'mrbase_sys_prompt',
        prompt_name: '',// 参照名称
        prompt_desc: '',// 参照描述
        source_table: '',// 数据来源表
        value_field: '',// 取值列
        view_fields: '',// 显示列
        user_filter_field: '',// 人员数据过滤字段，多个逗号分隔使用数据权限
        org_filter_field: '',// 单位数据过滤字段，多个逗号分隔使用数据权限
        prompt_type: '', // 分类
        orderby: '', // 排序
        prompt_field: [], // 参照内容
        client_type_uuid: [], // 客户端类型
        uuid: '',
        filter: ''
      },
      temp: {}, // 存查询出来的数据
      source_table_name: [], // 来源表名列表
      valArr: [], // 取值列 列表
      disabled: true,
      advanceReferTo: true,
      promptData: {}
    };
  },
  validations: {
    reqParams: {
      client_type_uuid: {
        required
      }
    }
  },
  created () { this.promptData = { prompt_name: 'table_name' } },
  async mounted () {
    await this.national('mrbase_sys_prompt')
    this.$nextTick(async () => {
      // 有uuid传过来 就是修改 没有就是新增，
      const name = this.$store.getters.subPage_name
      const uuid = this.$route.params.uuid || this.$store.state.uuid
      // 查询关联表
      // this.promptData = { prompt_name: '来源表名' }
      this.skLoading = true
      if (uuid) {
        this.$route.meta.title = name
        this.reqParams.prompt_name = name
        this.reqParams.uuid = uuid
        // 查询详情
        let detailObj = await this.getdetail()
        for (var key in detailObj) {
          for (var a in this.reqParams) {
            if (key == a) {
              this.reqParams[a] = detailObj[key]
            }
          }
        }
        if (this.reqParams.prompt_field) {
          this.reqParams.prompt_field = this.reqParams.prompt_field.split(',')
        }
        if (detailObj.client_type_uuid) {
          this.reqParams.client_type_uuid = this.reqParams.client_type_uuid.split(',')
        }
        this.temp = this.reqParams
        // 根据关联表获取列值
        const valArr = await this.search({ source_table: detailObj.source_table, prompt_name: 'relation_field' })

        this.valArr = valArr
        if (!this.$store.state.iconstatus) {
          this.disabled = false
        }
      } else {
        this.$route.meta.title = '新参照'
        this.disabled = false
      }
      this.skLoading = false
      // const searchArr = await this.search()
      // this.source_table_name = searchArr
    })

  },
  methods: {
    // 取消修改新增
    cancel () {
      const uuid = this.$route.params.uuid || this.$store.state.uuid
      if (uuid) {
        this.showText = this.$t('info.give_up_modify')
      } else {
        this.showText = this.$t('info.give_up_add')
      }

      this.isOpen = true
      this.is_return = true
    },
    // 提交修改新增
    async success () {
      // console.log(this.reqParams)
      // 有参照名传过来 就是修改 没有就是新增，
      const name = this.$route.params.name || this.$store.state.subPage_name
      // 判断 必填是否完成
      this.$v.reqParams.client_type_uuid.$touch()
      const isSubmit = this.$refs.advanceReferTo.validate()
      if (!isSubmit || this.$v.reqParams.client_type_uuid.$error) {
        // 不通过验证就去掉二次弹出 继续改格式验证
        this.is_return = false
        this.isOpen = false
        return
      }
      this.isLoading = true
      this.loading = true
      const obj = this.$clone(this.reqParams)
      obj.prompt_field = obj.prompt_field.join()
      obj.client_type_uuid = obj.client_type_uuid.join()
      if (name) {
        const res = await getChange(obj)
          .then(res => {
            this.isLoading = false
            if (res.code == 200) {
              this.$Message({ text: this.$t('info.edit_success') })
              this.$router.go(-1)
            }
            this.loading = false
            return res
          })
          .catch(err => {
            this.loading = false
            return err
          })
      } else {
        const res = await getAdd(obj)
          .then(res => {
            this.isLoading = false
            if (res.code == 200) {
              this.$Message({ text: this.$t('info.add_success') })
              this.$router.go(-1)
            }
            this.loading = false
            return res
          })
          .catch(err => {
            this.loading = false
            return err
          })
      }
    },
    // 点击删除此参照
    handleDel () {
      this.showText = this.$t('info.is_del')
      this.isOpen = true

    },
    // 二次确认 取消
    onCancel () {
      this.is_return = false
      this.isOpen = false
    },
    // 二次确认 确认
    onConfirm () {
      // 确认放弃修改
      if (this.is_return) {
        this.$router.go(-1)
      } else {
        this.isLoading = true
        // 确认删除
        const obj = Object.assign({}, this.params, { uuid: this.reqParams.uuid })
        getDetele(obj)
          .then(res => {
            this.isOpen = false
            this.isLoading = false
            this.$Message({ text: this.$t('info.del_success') })
            if (this.$parent.desserts.length === 1 && this.$parent.params.cpage > 1) {
              this.$parent.params.cpage--
              this.$parent.params.totalpage--
            }
            this.$router.go(-1)

          })
          .catch(err => {
            this.isLoading = false
            console.log(err)
          })
      }
    },
    // 点击修改icon
    handleEdit () {
      this.disabled = !this.disabled
    },
    // 点击返回上一页
    handleReturn () {
      this.$router.go(-1)
    },
    async search (params = {}) {
      // 高级参照业务列表
      // 查询来源表名
      const obj = Object.assign({}, this.params, { prompt_name: 'table_name' }, params)
      const arr = await getBusinessList(obj)

      return arr.data
    },
    // 获取详情
    async getdetail () {
      const obj = Object.assign({}, this.params, { uuid: this.reqParams.uuid })

      const arr = await getDetail(obj)
        .then(res => {
          if (res.code == 200) {
            return res.data
          } else {
            this.$Message({ text: res.msg })
          }
        })
        .catch(err => {
          console.log(err)
        })
      return arr
    },
    // 关联表值切换就去找下面关联的   prompt_name: '关联表字段' 这个每次要后端给 给死的
    async source_tableChange (val) {
      // this.reqParams.source_table = val
      const searchArr = await this.search({ source_table: val, prompt_name: 'relation_field' })
      // 来源表名切换  下面的几项重置
      this.reqParams.value_field = ''
      this.reqParams.view_fields = ''
      this.reqParams.orderby = ''
      this.reqParams.prompt_field = []
      this.valArr = searchArr
    }
  },
  computed: {
  },
  watch: {}
};
</script>

<style scoped>
.iconGroup {
  position: relative;
}
.returnBtn {
  color: #fff;
  position: absolute;
  top: -55px;
  right: 130px;
}
.editBtn {
  color: #fff;
  position: absolute;
  top: -55px;
  right: 70px;
}
.delBtn {
  color: #fff;
  position: absolute;
  top: -55px;
  right: 10px;
}
/* .btnGroup {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  margin: 0 auto;
  margin-top: 20px;
}
.successBtn {
  color: #fff;
}
.cencelBtn {
  color: #ffad4d;
} */
.advance /deep/ .v-menu__content {
  min-width: 400px !important;
}
</style>
